.headerContainer {
  background: grey;
  border-radius: 100px;
  animation: slideInTitleBlock 300ms forwards;
}
@keyframes slideInTitleBlock {
  0% {
    opacity: 0;
    transform: translateX(-500px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.headerSubContainer {
  position: relative;
  overflow: hidden;
  min-height: 80vh;
  width: 80vw;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}
.headerTitle {
  position: relative;
  z-index: 0;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 230px;
  padding: 1.5em;
}
.headerTitle h1 {
  text-align: center;
  color: white;
  font-size: 3em;
  font-weight: 300;
  letter-spacing: 4px;
  margin-bottom: 1em;
  opacity: 0;
  animation: headerTitleSlide 1300ms forwards;
  animation-delay: 300ms;
}
@keyframes headerTitleSlide {
  0% {
    opacity: 0;
    transform: translateY(-300px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.headerTitle h3 {
  text-align: center;
  font-size: 2em;
  letter-spacing: 2px;
  color: white;
  font-weight: 300;
  opacity: 0;
  animation: headerSubTitleSlide 1500ms forwards;
  animation-delay: 600ms;
}
@keyframes headerSubTitleSlide {
  0% {
    opacity: 0;
    transform: translatex(-300px);
  }
  100% {
    opacity: 1;
    transform: translatex(0);
  }
}
.headerAbout {
  flex: 3;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-width: 200px;
  position: relative;
  overflow: hidden;
  background: #292929;
}
.headerAbout p {
  font-family: "Josefin Sans", sans-serif;
  max-height: 80%;
  max-width: 1600px;
  flex: 1;
  text-align: center;
  color: white;
  font-size: 2em;
  padding: 1.5em;
  line-height: 1.6em;
  width: 100%;
  z-index: 1;
  text-shadow: 1px 1px 1px black;
  animation: headerTextSlideIn 2s forwards;
  animation-delay: 2s;
  opacity: 0;
}
@keyframes headerTextSlideIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.lightbulbImg {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  z-index: 0;
  filter: blur(8px) brightness(0.7);
  transform: scale(1.08);
  opacity: 0;
  animation: fadeinHeaderImg 3000ms forwards;
  animation-delay: 500ms;
}
@keyframes fadeinHeaderImg {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media screen and (max-width: 900px) {
  .headerContainer {
    border-radius: 0;
  }
  .headerSubContainer {
    width: 100vw;
  }
  .headerTitle h1 {
    font-size: 2em;
  }
  .headerTitle h3 {
    font-size: 1em;
  }
  .headerAbout p {
    font-size: 1em;
    line-height: 2em;
  }
}
.headerLinks {
  margin-top: 1em;
  display: flex;
}
.headerLinks a {
  padding: 8px 8px;
  color: white;
  margin: 4px;
  text-decoration: none;
  opacity: 0;
  animation: fadeInHeaderLinks 1500ms forwards;
  border: 1px solid white;
  color: white;
  border-radius: 5px;
  font-size: 0.9em;
}
.headerLinks a:nth-child(1) {
  animation-delay: 1500ms;
}
.headerLinks a:nth-child(2) {
  animation-delay: 1700ms;
}
.headerLinks a:nth-child(3) {
  animation-delay: 1900ms;
}
.headerLinks a:hover {
  background: #707070;
}
.arrowDown {
  margin: 20px;
  animation: arrowBounce 600ms alternate infinite;
  animation-delay: 2500ms;
  transform: translateY(35px);
}
.arrowDownContainer {
  opacity: 0;
  animation: arrowFadeIn 2s forwards;
  animation-delay: 2000ms;
}
@keyframes arrowFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.arrowDown a {
  color: #e8dd44;
  font-size: 3em;
}
@keyframes arrowBounce {
  0% {
    transform: translateY(35px);
  }
  100% {
    transform: translateY(25px);
  }
}
@keyframes fadeInHeaderLinks {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
