#contactContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-height: 50vh;
}
#contactContainer h1 {
  margin: 0 auto;
  margin-top: 20px;
  letter-spacing: 4px;
  font-weight: 600;
  font-size: 2em;
}
#contactSubContainer {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap-reverse;
  padding: 10px;
  max-width: 1400px;
}
#aboutText {
  flex: 1.7;
  min-width: 260px;
  margin: 20px;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#aboutText h3 {
  font-size: 3em;
  text-align: left;
  width: 100%;
  font-family: "Josefin Sans", sans-serif;
  margin-bottom: 15px;
}
#aboutText p {
  line-height: 1.7em;
  padding: 0px;
  font-size: 1.2em;
}
#picOfMe {
  flex: 1;
  max-width: 50%;
  min-width: 220px;
  margin: 20px auto;
}

#aboutTitleInner {
  display: block;
  font-size: 3em;
  text-align: left;
  width: 100%;
  margin-bottom: 15px;
}
#aboutTitleOuter {
  display: none;
  font-size: 3em;
  text-align: center;
  width: 100%;
}
@media screen and (max-width: 768px) {
  #aboutText p {
    font-size: 1em;
    line-height: 1.5em;
  }
  #aboutTitleInner {
    display: none;
  }
  #aboutTitleOuter {
    display: block;
  }
}
