.portfolioContainer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 0;
}
.portfolioTitle {
  color: #333;
  width: 100%;
  background: #fff;
  text-align: center;
  letter-spacing: 4px;
  font-weight: 500;
  padding: 20px 0;
  margin-bottom: 2em;
}
.portfolioTitle h1 {
  font-family: "Josefin Sans", sans-serif;
}
.portfolioGrid {
  display: grid;
  width: 80%;
  max-width: 2000px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: center;
  grid-gap: 3em;
  padding-bottom: 3em;
}
@media screen and (max-width: 700px) {
  .portfolioGrid {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(1, minmax(100px, 1fr));
    grid-column-gap: 5em;
  }
}
.buttonsContainer {
  max-width: 1200px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 2em;
}

.loadingScreen {
  width: 100vw;
  height: 100vh;
  top: 0;
  position: fixed;
  z-index: 100;
  display: none;
}
.loadingScreenText {
  font-size: 2em;
  font-weight: 300;
  color: #eee;
  position: absolute;
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  opacity: 0;
  z-index: 99;
}
@media screen and (max-width: 600px) {
  .loadingScreenText {
    font-size: 1.3em;
  }
}
.loadingBottomSlider {
  background: #202020;
  height: 50%;
  width: 100vw;
  bottom: 0;
  transform: translateX(110%);
  position: absolute;
  z-index: 98;
}
.loadingTopSlider {
  background: #252525;
  height: 50%;
  width: 100vw;
  top: 0;
  transform: translateX(-110%);
  position: absolute;
  z-index: 98;
}

.loadingScreenVisible {
  display: block;
}
.loadingSlidersSlideInLeft {
  animation: slidersSlideInLeft 2500ms;
}
@keyframes slidersSlideInLeft {
  0% {
    transform: translateX(-110%);
  }
  20% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(0);
  }
  100% {
    transform: translateY(-110%);
  }
}
.loadingSlidersSlideInRight {
  animation: slidersSlideInRight 2500ms;
}
@keyframes slidersSlideInRight {
  0% {
    transform: translateX(110%);
  }
  20% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(0);
  }
  100% {
    transform: translateY(110%);
  }
}
.loadingTextFadeIn {
  opacity: 0;
  animation: textFadeIn 1250ms;
}
@keyframes textFadeIn {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
