.navbar {
  position: fixed;
  width: 80vw;
  height: 50px;
  transform: translateY(15px);
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  z-index: 10;
}
.menuButton {
  cursor: pointer;
  color: #ccc;
  font-size: 40px;
  margin-right: 1em;
  animation: slideInMenuButton 4000ms forwards;
}
.menuButton:hover {
  color: white;
}
@keyframes slideInMenuButton {
  0% {
    transform: translateX(-100vw);
  }
  100% {
    transform: translateX(0);
  }
}
@media screen and (max-width: 900px) {
  .navbar {
    width: 100vw;
  }
}

.menu {
  background: rgb(29, 29, 29, 1);
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: 9;
  display: none;
  justify-content: center;
  align-items: center;
  transition: all 200ms;
}
.menuVisible {
  display: flex;
  animation: slideMenuDown 200ms forwards;
}
@keyframes slideMenuDown {
  0% {
    transform: translateY(-100vh);
  }
  100% {
    transform: translateY(0);
  }
}
.menuLinks {
  width: 100%;
  max-width: 600px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.menuNavigationLinks {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.menuLinkedInGithubLinks {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.menuLinkedInGithubLinks a {
  padding: 5px 10px;
  margin: 10px;
  text-decoration: none;
  color: white;
  border: 1px solid white;
  border-radius: 5px;
}
.menuLinkedInGithubLinks a:hover {
  background: #494949;
}
.menuLinkedInGithubLinks a:nth-child(3) {
  transform: translateY(-100vh);
  animation: linksDropIn 1000ms forwards;
  animation-delay: 200ms;
}
.menuLinkedInGithubLinks a:nth-child(2) {
  transform: translateY(-100vh);
  animation: linksDropIn 1000ms forwards;
  animation-delay: 300ms;
}
.menuLinkedInGithubLinks a:nth-child(1) {
  transform: translateY(-100vh);
  animation: linksDropIn 1000ms forwards;
  animation-delay: 400ms;
}
.menuBrand h1 {
  padding: 5px 10px;
  margin: 10px;
  font-weight: 300;
  color: white;
  letter-spacing: 10px;
}

.menuBrand {
  transform: translateY(-100vh);
  animation: linksDropIn 1000ms forwards;
  animation-delay: 600ms;
}

.slidingMenu a {
  text-decoration: none;
  color: white;
  font-size: 2em;
  padding: 20px;
  margin: 20px;
  text-align: center;
}
@media screen and (max-width: 600px) {
  .slidingMenu a {
    font-size: 1.5em;
  }
}

.slidingMenu:nth-child(1) {
  animation: menuSlideIn 1000ms forwards;
  animation-delay: 200ms;
  opacity: 0;
}
.slidingMenu:nth-child(2) {
  animation: menuSlideIn 1000ms forwards;
  animation-delay: 300ms;
  opacity: 0;
}
.slidingMenu:nth-child(3) {
  animation: menuSlideIn 1000ms forwards;
  animation-delay: 400ms;
  opacity: 0;
}

@keyframes menuSlideIn {
  0% {
    opacity: 0;
    transform: translatex(-100%);
  }
  100% {
    opacity: 1;
    transform: translatex(0%);
  }
}
@keyframes linksDropIn {
  0% {
    opacity: 0.5;
    transform: translateY(-100vh);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
