.portfolioSectionContainer {
  max-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 7px 7px 5px black;
}
.portfolioHeader {
  font-family: "Josefin Sans", sans-serif;
  color: #fff;
  padding: 5px 0;
  font-weight: 300;
  letter-spacing: 4px;
  width: 100%;
  text-align: center;
  font-size: 1.5em;
  background: #1b2a37;
  border-bottom: 3px solid black;
}
.portfolioImgContainer {
  display: flex;
  justify-content: center;
  position: relative;
  overflow-x: hidden;
  cursor: pointer;
}
.portfolioImg {
  max-width: 300px;
  width: 300px;
  max-height: 250px;
  height: 100%;
}
.portfolioImgLinks {
  display: none;
  position: absolute;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: all 300ms;
}
.portfolioImgLinks a {
  padding: 10px;
  color: #1b2a37;
  font-weight: 300;
  font-size: 1.2em;
  text-decoration: none;
  cursor: pointer;
}
.portfolioLinksVisible {
  display: flex;
  background: rgb(250, 250, 250, 0.9);
  animation: fadeInPortfolioLinks 500ms forwards;
  cursor: default;
}

@keyframes fadeInPortfolioLinks {
  0% {
    height: 0;
  }
  100% {
    height: 100%;
  }
}
