button {
  position: relative;
  background: transparent;
  border: none;
  margin: 10px;
  width: 85px;
  padding: 5px 10px;
  border-radius: 2px;
  box-shadow: 0px 4px 5px #000;
  cursor: pointer;
  transition: all 200ms;
  z-index: 1;
  overflow: hidden;
}
.buttonText {
  position: relative;
  z-index: 2;
  color: #ccc;
  font-weight: 300;
  font-size: 1.2em;
}
@media screen and (max-width: 600px) {
  .buttonText {
    position: relative;
    z-index: 2;
    color: #ccc;
    font-weight: 300;
    font-size: 1em;
  }
}
@media screen and (max-width: 400px) {
  button {
    width: 60px;
  }
  .buttonText {
    font-size: 0.8em;
  }
}
button:hover {
  color: #fff;
}
button:hover .topSlider {
  transform: translateX(0%);
}
button:hover .bottomSlider {
  transform: translateX(0%);
}

.topSlider {
  height: 50%;
  width: 100%;
  background: #454545;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateX(110%);
  z-index: 1;
  transition: all 300ms;
}
.bottomSlider {
  height: 50%;
  width: 100%;
  background: #555555;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-110%);
  z-index: 1;
  transition: all 300ms;
}
.buttonBackground {
  position: absolute;
  background: #353535;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}
