.contactContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5em;
  padding-bottom: 8em;
}
.contactContainer p {
  color: white;
  max-width: 1200px;
  min-width: 300px;
  font-size: 1.2em;
  letter-spacing: 2px;
  line-height: 1.5em;
  text-align: center;
  margin-bottom: 20px;
}
.email {
  display: flex;
  align-items: center;
  justify-content: center;
}
.email a {
  color: #191919;
  font-size: 1.5em;
  padding: 8px 25px;
  margin: 0.8em;
  font-weight: 300;
  border-radius: 20px;
  box-shadow: 1px 3px 5px black;
  background: white;
  text-decoration: none;
}
.email h1 {
  color: white;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  margin-left: 1em;
  text-align: center;
}
@media screen and (max-width: 768px) {
  .email a {
    font-size: 0.9em;
  }
  .email h1 {
    font-size: 1em;
  }
  .contactContainer p {
    font-size: 1em;
    text-align: left;
  }
}
