.footerContainer {
  max-width: 1600px;
  margin: 0 auto;
  width: 100%;
  min-height: 15em;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;
}
.footerContainer p {
  font-weight: 300;
  color: gray;
}
.footerContainer ul {
}
.footerContainer li {
  list-style: none;
  display: inline-block;
  padding: 1em;
}
.footerContainer a {
  color: gray;
  text-decoration: none;
  font-weight: 300;
}
.pictureOfMe {
  max-width: 150px;
  position: absolute;
  border-radius: 50%;
}
@media screen and (max-width: 800px) {
  .pictureOfMe {
    transform: translateY(-80px);
    width: 100px;
  }
  .footerContainer a {
    font-size: 0.8em;
  }
  .footerContainer p {
    font-size: 0.8em;
  }
}
